.arrivals {
  padding-top: 5rem;
  padding-bottom: 2.5rem;
}
.arrivals-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
}
.arrivals-picture {
  width: 24%;
}
.arrivals-picture img {
  width: 100%;
  height: 100%;
}
.arrivals__title {
  font-weight: 500;
  font-size: 1.4rem;
  line-height: 1.8rem;
  letter-spacing: 0.04em;
  color: #000000;
  text-align: center;
  margin-bottom: 2.2rem;
}
